export default {
	data() {
		return {
			itemList:[],
			shopid: ''
		}
	},
	onLoad:function(option){
		this.shopid = option.shopid
	},
	onShow:function(){
		var that = this
		uni.request({
			url: this.$bashUrl+ '/index.php/home/Api/cash_list/',
			method: 'POST',
			data:{
				id: this.shopid,
			},
			success:(res=>{
				console.log(res)
				that.itemList = res.data
				console.log(that.itemList)
			})
		})
	},
	methods: {
		
	}
}
